@use '../../foundation/mixin' as mixin;

// 文字や数字、記号（▶︎）に関するスタイル

// サブタイトルの数字
.c-topnumber {
    &__red ,&__yellow ,&__orange  {
        font-size: 96px;

        @include mixin.smartphone {
            font-size: 48px;
        }
    }

    &__red {
        color: #FF3333; 
    }

    &__yellow {
        color: #FFD433;
    }

    &__orange {
        color: #FFAD33;
    }

    
}

// サブタイトルの文字の大きさ
.c-topsubtitle {
    font-size: 64px;

    @include mixin.smartphone {
        font-size: 24px;
    }
}

// お知らせのカテゴリーの大きさ
.c-categoly {
    &__red , &__yellow, &__orange, &__date {
        width: 120px;
        height: 31px;

        >p {
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;

        }
    
    }  

    &__largeorange  {
        width: 200px;
        height: 32px;

        @include mixin.smartphone {
            width: 150px;
            height: 25px;
        }

        >p {
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 24px;
            color: white;

            @include mixin.smartphone {
                font-size: 16px;
            }

        }
    
    }  

    &__date {
         >p {
             justify-content: end;
         }
    }

    &__red {
        background-color: #FF3333;
    }

    &__yellow {
        background-color: #FFD433;
    }

    &__orange {
        background-color: #FFAD33;
    }   

    &__largeorange {
        background-color: #FFAD33;
    }

}

// toppageのグルメの横棒のあしらい

.c-topfood { 
    &__title {
        font-size: 24px;
    }
}

.c-newsdetail {
    &__title {
        font-size: 32px;

        @include mixin.smartphone {
            font-size: 24px;
        }
    }

    &__list {
        list-style:inside
    }
}

// 各ページのタイトル
.c-pagetitle {
    font-family: 'Zen Kaku Gothic New', sans-serif;
    font-size: 48px;
    @include mixin.smartphone {
        font-size: 24px;
    }
}


// お店一覧のリンクの矢印の色
.c-allow {
    &__orange {
       color :  #FFAD33;
    }

    &__red {
       color :  #FF3333;
     }

} 

// 場所のラベルのスタイリング
.c-location {
    &__orange, &__deeporange, &__red , &__yellow, &__white {
        display: flex;
        align-items: center;

    }

    &__orange, &__deeporange, &__red , &__yellow, &__white {
        > P {
            width: 160px;
            height: 30px;
            text-align: center;
        }
    }

    &__orange {
        >p {
            background: rgba(255, 173, 51, 0.25);
        }
    }

    &__deeporange {
        >p {
            background: #FFAD33;
        }
    }



    &__red {
        >p {
            background: rgba(255, 51, 51, 0.25);
        }
    }

    &__yellow {
        >p {
            background:#FFF4CC;
        }
    }

    &__white {
        >p {
            background:white;
        }
    }
}

//リンクを太くするためのスタイル
.c-link {
    font-weight: bold;
} 

//Tiktokのテキスト
.c-tiktok {
    &__yellow {
        font-size: 24px;
        color: #FFAD33;
    }

    &__red {
        font-size: 24px;
        color: #FF3333;
    }
}

//WeLove心斎橋の入会について
.c-admission {
    &__price {
        font-size: 24px;
    }

    &__must {
        background-color: #FFAD33;
        border-radius: 10px;
        font-size: 14px;
        width: 45px;
        height: 30px;
        text-align:center;
        line-height:30px;
        color: white;
        
    }

    &__largebox ,&__middlebox ,&__shortbox {
        border: .5px solid #303030;
        border-radius: 2px;
        height: 30px;
       
    }

    &__largebox {
        width: 440px;
    }

    &__middlebox {
        width: 210px;
    }

    &__shortbox {
        width: 60px;
    }

    &__thankyou {
        font-size: 48px;
        font-weight: bold;
    }

    &__text {
        width: 920px;
    }

}

//WeLove心斎橋の問い合わせ
.c-inquiry {
    &__textarea, &__commentarea {
        border-radius: 2px;
        height: 120px;
        width: 440px;
    }

    &__textarea  {
        border: .5px solid #303030;
        
    }

    &__commentarea {
       display: flex;
       align-items: center;
    }

}

//利用規格
.c-regulation {
    &__title {
        font-size: 24px;
    }

    &__list {
        list-style-type: decimal;
        width: 880px;
    }
}